.utilityContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 100px;
}

.utilityBanner {
  height: fit-content;
  display: flex;
  flex-direction: column-reverse;
  align-content: center;
}

.bannerImage {
  object-fit: contain;
  align-items: center;
}

.utilityHeader {
  font-size: 30px;
  font-weight: bold;
  color: black;
  padding-top: 50px;
  margin-bottom: 10px;
}

.utilityBody {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  color: black;
}

.textHeader {
  font-size: 48px;
  font-weight: bolder;
  color: black;
  text-align: left;
}

.gallerytextBody {
  font-weight: normal;
  margin-top: 10px;
  margin-bottom: 10px;
  text-align: left;
  font-size: 14px;
}

.imagebox {
  max-height: 250px;
}

.Definition {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.tokenAirdrop {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 20px;
}

.Community {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-top: 100px;
}

.IDOContent {
  display: flex;
}

.IDOBox {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin: 25px;
  background-color: #fdc8d7;
  border-radius: 20px;
  align-items: center;
  height: fit-content;
}

.IDOBoxImage {
  display: flex;
  justify-content: center;
}

.IDOBoxImageSize {
  display: flex;
  justify-content: center;
  padding-left: 40px;
  padding-right: 40px;
}

.IDOBoxContent {
  display: flex;
  flex-direction: column;
  background-color: white;
  border: 2px solid white;
  border-radius: 20px;
  padding-left: 30px;
  padding-right: 30px;
  height: 250px;
  width: 250px;
  font-size: 14px;
}

.IDOBoxContentHeader {
  display: flex;
  justify-content: center;
  font-size: 24px;
  font-weight: bold;
}

.IDOBoxContentText {
  display: flex;
  flex-direction: column;
}

.galleryBox {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-evenly;
  margin-top: 75px;
  padding: 25px;
  max-width: 1100px;
}

.galleryTextHeader {
  font-size: 48px;
  font-weight: bolder;
  color: black;
  margin-bottom: 30px;
}

.galleryContent {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 30px;
}

.galleryText {
  display: flex;
  flex-direction: column;
  justify-content: center;
  font-size: 14px;
}

.logoBox {
  display: flex;
}

.galleryImages {
  display: flex;
  padding: 10px;
  max-width: 60%;
}

.comingSoon {
  display: flex;
  flex-direction: row;
}

.soonHeader {
  display: flex;
  flex-direction: column;
  font-size: 48px;
  font-weight: bolder;
  color: black;
  text-align: left;
  align-items: center;
  justify-content: flex-start;
}

.soonContent {
  display: flex;
  flex-direction: column;
}

.soonBox {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin: 25px;
  background-color: #fdc8d7;
  border-radius: 20px;
  align-items: center;
  height: fit-content;
}

.comingSoonImageSize {
  max-height: 300px;
}

.soonBoxImage {
  display: flex;
  justify-content: center;
}

.soonBoxImageSize {
  display: flex;
  justify-content: center;
  padding-left: 40px;
  padding-right: 40px;
}

.soonBoxContent {
  display: flex;
  flex-direction: column;
  background-color: white;
  border: 2px solid white;
  border-radius: 20px;
  padding-left: 40px;
  padding-right: 40px;
  height: 200px;
  width: 500px;
  font-size: 14px;
}

.soonContentHeader {
  display: flex;
  justify-content: center;
  font-size: 24px;
  font-weight: bold;
}

.soonBoxContentText {
  display: flex;
  flex-direction: column;
  text-align: left;
}

